import React from 'react';
import { VerticalTimeline, VerticalTimelineElement } from 'react-vertical-timeline-component';
import HorizontalList from 'Content/HorizontalList';

import Showdown from 'showdown';
import _ from 'lodash';

import 'react-vertical-timeline-component/style.min.css';
import './WorkHistory.scss';

import QantasLogo from 'Content/Resume/images/qantas.png';
import CourseraLogo from 'Content/Resume/images/coursera.png';
import ApnLogo from 'Content/Resume/images/apn.png';
import ApgLogo from 'Content/Resume/images/apg.jpg';
import MocraLogo from 'Content/Resume/images/mocra.png';
import ActionCoachLogo from 'Content/Resume/images/actioncoach.png';
import ScrumAllianceLogo from 'Content/Resume/images/ScrumAlliance.png';
import LaTrobeLogo from 'Content/Resume/images/LaTrobe.jpg';
import NextElementLogo from 'Content/Resume/images/NextElement.png';
import antlerLogo from 'Content/Resume/images/antler.png';

import jsonData from '../data/resume.json';

const converter = new Showdown.Converter();

const WorkIcon = () => {
  return (<i className="fas fa-briefcase"></i>)
}

const EducationIcon = () => {
  return (<i className="fas fa-graduation-cap"></i>)
}

const StarIcon = () => {
  return(<svg className="jss22" focusable="false" viewBox="0 0 24 24" aria-hidden="true"><path d="M12 17.27L18.18 21l-1.64-7.03L22 9.24l-7.19-.61L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21z"></path></svg>)
}

export default class Resume extends React.Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.loadMore = this.loadMore.bind(this);
    this.jsonData = this.props.jsonData || jsonData || []

    this.components = {
      work: WorkIcon,
      education: EducationIcon
    }

    this.logos = {
      QantasLogo,
      ApnLogo,
      ApgLogo,
      MocraLogo,
      ActionCoachLogo,
      CourseraLogo,
      LaTrobeLogo,
      ScrumAllianceLogo,
      NextElementLogo,
      antlerLogo
     }

    this.state = {
      allTimeline: this.jsonData,
      timeline: this.jsonData,
      selectedCategory: '*',
      categories: _.uniq(this.jsonData.map((item) => { return item.type }))
    }
  }

  renderFilters() {
    if (this.state.categories.length > 1) {
      return (
        <div id="filters-container" className="top_30 row">
          <div className="col-12">
            <h4>Filters</h4>
            <p>Use the filters or view my history in full</p>
            <HorizontalList name="All" onClick={() => this.handleClick("*")} active={this.state.selectedCategory === "*"} />
            {this.state.categories.map((category, index) => {
              return <HorizontalList name={category} key={index} onClick={() => this.handleClick(category)} active={this.state.selectedCategory === category} />
            })}
          </div>
        </div>
      )
    }
  }

  handleClick(value) {
    if(value === '*') {
      this.setState({
        timeline: this.state.allTimeline,
        selectedCategory: '*'
      })
    } else {
      this.setState({
        timeline: this.state.allTimeline.filter((item) => { return item.type === value }),
        selectedCategory: value
      })
    }    
  }

  loadMore(event) {

    this.setState({
      timeline: this.state.allTimeline
    })
  }

  logoComponent(src, alt = "") {
    const LogoComponent = this.logos[src];
    
    if(src) {
      return (
        <img src={LogoComponent} className="" alt={alt} />
      )
    }
  }

  Icon(type) {
    const IconComponent = this.components[type] || WorkIcon
    return (< IconComponent />);
  }

  showMoreButton() {
    if(this.state.timeline.length === this.state.allTimeline.length) {
      return(<VerticalTimelineElement
         className="timeline-element-last"
         iconStyle={{ background: 'rgb(16, 204, 82)', fill: '#ffffff' }}
         icon={<StarIcon />}
       />)
    } else {
      return(<VerticalTimelineElement
        className="timeline-element-last"
        icon={<button className="btn btn-primary load-more-timeline-events" onClick={this.loadMore}>Load more</button>}
      />)
    }
  }

  render() {
    return (
      <div id="">
        <div id="resume">
          <div className="row">
            <section className="col-12">
              <div className="row">
                <div className="working-history col-md-12 padding_15 padbot_30">
                  <VerticalTimeline layout="1-column">
                    {this.state.timeline.map((resume, index) => {
                      let content = { __html: converter.makeHtml(resume.content) }
                      let skills = resume.skills || []
                      let resumeTitle = { __html: resume.title.replace("\n", "<br />") }

                      return <VerticalTimelineElement
                        key={index}
                        className={resume.className}
                        date={resume.date}
                        iconStyle={resume.iconStyle}
                        icon={this.Icon(resume.type)} >
                        <div className="row">
                          <div className="col-12 d-flex">
                            {this.logoComponent(resume.logo)}
                            <div className="px-2">
                              <h3 className="vertical-timeline-element-title">{resume.company}</h3>
                              <h4 className="vertical-timeline-element-subtitle" dangerouslySetInnerHTML={resumeTitle}></h4>
                            </div>
                          </div>
                        </div>
                        <p dangerouslySetInnerHTML={content}></p>
                        <div className="working-history-skills">
                          {skills.map((skill, skillIndex) => {
                            return( <div key={skillIndex} className="skill-pill">{skill}</div> )
                          })}
                        </div>
                      </VerticalTimelineElement>
                    })}
                    {this.showMoreButton()}
                  </VerticalTimeline>

                </div>
              </div>
            </section>
          </div>
        </div>
      </div>
    );
  }
}
