import applicationsData from 'data/applications.json';
import _ from 'lodash';

class ApplicationsRepository {
  constructor() {
    this.applications = applicationsData || [];
  }

  /* usage
   * const applications = applicationRepository
   * applications.load('myer', (application) => { ... })
   */
  load(matchon, callback) {
    const application = _.find(this.applications, (application) => application.matchon === matchon )
    callback(application)
  }
}

export default new ApplicationsRepository();
