import React from "react";
import ReactGA from 'react-ga';

import './ContactUs.scss'

export default class ContactUs extends React.Component {
  static defaultProps = {
    formSource: ''
  }

  constructor(props) {
    super(props);
    this.state = {
      status: ""
    };

    this.submitForm = this.submitForm.bind(this);
  }

  render() {
    const { status } = this.state;

    return (
      <form
        className="contact-us-form"
        onSubmit={this.submitForm}
        action="https://formspree.io/mqkywgbn"
        method="POST"
      >
        <div className="form-group">
          <label htmlFor="contactUsEmail">Your Email:</label>
          <input id="contactUsEmail" type="email" name="email" className="form-control" />
        </div>
        <div className="form-group">
          <label htmlFor="contactUsName">Your Name:</label>
          <input id="contactUsName" type="name" name="name" className="form-control" />
        </div>
        <div className="form-group">
          <label htmlFor="contactUsMessage">Message:</label>
          <textarea className="form-control" id="contactUsMessage" name="message" rows="3"></textarea>
        </div>
        <input id="source" type="hidden" name="source" className="form-control" value={this.props.formSource} />
        {status === "SUCCESS" ? <p>Thanks, your message has been sent!</p> : <button className="btn btn-primary">Send</button>}
        {status === "ERROR" && <p>Ooops! There was an error.</p>}
      </form>
    );
  }

  submitForm(ev) {
    ReactGA.event({
      category: 'Resume',
      action: 'submit contact',
      label: 'biteable'
    });

    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }
}