import React, { Component } from 'react';
import ReactPlayer from 'react-player'
import WorkHistory from './WorkHistory'
import ReactGA from 'react-ga';

import ContactUs from 'ContactUs';
import applicationsRepository from 'libs/ApplicationsRepository'

import 'react-vertical-timeline-component/style.min.css';
import './Cv.scss'
import profilePicture from "Profile/mark.jpg"
import skillRadar from './skill-radar-lexer.png'

import biteableResume from './data/resume/mark_gandolfo_resume_biteable.pdf'
import lexerResume from './data/resume/mark_gandolfo_resume_lexer.pdf'
import defaultResume from './data/resume/mark_gandolfo_resume.pdf'

class Cv extends Component {
  constructor(props) {
    super(props)
    const companyNameParam = this.props.match.params.company
    
    applicationsRepository.load(companyNameParam, (application) => {
      if( application === undefined) {
        /* not found, go back to the homepage */
        window.location = '/'
      } else {
        this.state = {
          matchon: application.matchon,
          companyName: application.companyName,
          resume: application.resume,
          positionTitle: application.positionTitle,
          pitch: { __html: application.pitch.replace('\n', '<br />') },
          videoLink: application.videoLink,
          aboutMe: application.aboutMe
        }
      }
    })

    this.scrollToPlay = this.scrollToPlay.bind(this)
    this.trackDownloadResume = this.trackDownloadResume.bind(this)
    this.trackVideoPlay = this.trackVideoPlay.bind(this)
  }

  componentDidMount() {
    document.title = `Hello ${this.state.companyName} | Mark Gandolfo`
    console.log('%c Hello ' + this.state.companyName +'!!\n', 'font-weight: bold; font-size: 50px;color: red; text-shadow: 3px 3px 0 rgb(217,31,38) , 6px 6px 0 rgb(226,91,14) , 9px 9px 0 rgb(245,221,8) , 12px 12px 0 rgb(5,148,68) , 15px 15px 0 rgb(2,135,206) , 18px 18px 0 rgb(4,77,145) , 21px 21px 0 rgb(42,21,113)');
    console.log("Thanks for taking the time to review my cover letter\nPlease take a sec to read my resume and get in contact\nI love what the team are doing and would love to join\n\nMark")                                              
  }

  resumeLink() {
    const resumes = {
      biteableResume,
      lexerResume,
      defaultResume
    }

    return resumes[this.state.resume]
  }

  scrollToPlay(event) {
    const videoSection = document.getElementById('video-section')
    if (videoSection !== null) {
      const bounding = videoSection.getBoundingClientRect()
      window.scrollTo(bounding.x, bounding.y)
    }
  }

  scrollToContact(event) {
    const contactSection = document.getElementById('contact-section')
    if (contactSection !== null) {
      const bounding = contactSection.getBoundingClientRect()
      window.scrollTo(bounding.x, bounding.y)
    }
  }

  trackDownloadResume() {
    ReactGA.event({
      category: 'Resume',
      action: 'download cv',
      label: this.state.matchon
    });
  }

  trackVideoPlay() {
    ReactGA.event({
      category: 'Resume',
      action: 'video play',
      label: this.state.matchon
    });
  }

  anchorFor(link) {
    return `#/hello/${this.state.matchon}#${link}`
  }

  renderVideoSection() {
    if(this.state.videoLink !== undefined) {
      return(
        <div id="video-section" name="video" className="section video-section d-flex flex-column justify-content-center">
          <div className="halo d-flex flex-row justify-content-center video-container">
            <ReactPlayer
              onPlay={this.trackVideoPlay}
              className="video"
              height='auto'
              url={this.state.videoLink}
            />
          </div>
          <hr />
        </div>
      )
    } else {
      return (<div className="pb-5"></div>)
    }
  }

  renderAboutMe() {
    if(this.state.aboutMe !== undefined) {
      return(
        <div className="section col-12">
          <p className="offset-lg-3 col-lg-6 col-sm-12 about-me" dangerouslySetInnerHTML={{"__html": this.state.aboutMe }}></p>
          <hr />
        </div>
      )
    } else {
      return (<div className="pb-5"></div>)
    }
  }

  renderPlayOnProfile() {
    if (this.state.videoLink !== undefined) {
      return (<div className="play">
        <a href={this.anchorFor('video')} onClick={this.scrollToPlay} >
          <i className="fa fa-play" aria-hidden="true"></i>
        </a>
      </div>)
    }
  }

  render() {
    const windowHeight = `${window.innerHeight - 70}px`;

    return (
      <React.Fragment>
        <div className="hello-welcome">
          <div className="welcome">
            <nav className="navbar navbar-expand-lg navbar-light bg-light bg-transaprent">
              <h1>
                Hello {this.state.companyName}
              </h1>
              <div className="download-resume-in-nav d-flex">
                <form className="form-inline my-2 my-lg-0">
                  <a href={this.anchorFor('contact')} onClick={this.scrollToContact} rel="noopener noreferrer" className="nav-link my-2 my-sm-0 d-none d-sm-block">Contact</a>
                  <a href={this.resumeLink()} target="_blank" onClick={this.trackDownloadResume} rel="noopener noreferrer" className="btn btn-outline-primary btn-white my-2 my-sm-0">Download My Resume</a>
                </form>
              </div>
            </nav>

            {/* Main header content section */}
            <div className="content content-dark-background d-flex flex-column container-fluid" style={{height: windowHeight}}>
              <div className="d-flex content-dark-background-container">
                <div className="introText d-flex col-12 col-md-6">
                  <div className="dark-background">
                    <h3>Mark Gandolfo</h3>
                    <h4>{this.state.positionTitle}</h4>
                    <p dangerouslySetInnerHTML={this.state.pitch}></p>
                  </div>
                </div>
                <div className="introProfilePicture align-items-center justify-content-center">
                <div className="halo">
                  <div className="round-clipper">
                    <img src={profilePicture} alt="Mark" />
                    {this.renderPlayOnProfile()}
                  </div>
                </div>
                </div>
              </div>
              {/* there is more content down there (arrow) */}
              <div className="d-none d-md-flex col-md-12 flex-grow-0 justify-content-center down-arrow-section">
                <a href={this.anchorFor('video')} onClick={this.scrollToPlay} >
                  <i className="downArrow fa fa-angle-down" aria-hidden="true"></i>
                </a>
              </div>
            </div> 
          </div>
            
            {/* Video Section */}
            {this.renderVideoSection()}
            {this.renderAboutMe()}

            {/* Skill Section */}
            <div className="section skills-section d-flex flex-column justify-content-center">
              <h2>My Skills &amp; Experience</h2>
              <div className="row">
                <div className="col-12 d-flex justify-content-center">
                  <p>A brief overview, please see my resume for more detail</p>
                </div>
                <div className="col-sm-12 col-md-6">
                  <WorkHistory />
                </div>
                <div className="col-sm-12 col-md-6 skills-radar">
                  <img width="600px" className="img-fluid" height="auto" src={skillRadar} alt="Skills" />
                </div>
              </div>

              <hr />
            </div>

            {/* Footer Section */}
            <div id="contact-section" className="section contact-us-section d-flex justify-row row" style={{padding: '20px'}}>
              <div className="col-sm-12 col-md-6  px-2 contact-information">
                <h4>Want to get in contact?</h4>
                <p><a href={this.resumeLink()} target="_blank" onClick={this.trackDownloadResume} rel="noopener noreferrer" className="btn-me btn-outline-primary btn-white my-2 my-sm-0">Download My Resume</a></p>
                <p> <a href="tel:0407021014">0407 021014</a></p>
                <p> <a href="mailto:mark@gandolfo.com.au">mark@gandolfo.com.au</a></p>
                <p><a href="https://calendly.com/mark-10245/catch-up-with-mark"  target="_blank" rel="noopener noreferrer">Schedule a chat</a></p>
                <p>
                  <a href="https://www.linkedin.com/in/magandolfo/" title="linkedin" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin"></i></a>
                  <a href="https://twitter.com/markgandolfo/" title="twitter" target="_blank" rel="noopener noreferrer"><i className="fab fa-twitter"></i> </a>
                  <a href="https://medium.com/@mark_48113/" title="medium" target="_blank" rel="noopener noreferrer"><i className="fab fa-medium"></i> </a>
                </p>
              </div>
              <div className="col-sm-12 col-md-6 justify-content-flex-start">
                <div className="col-sm-12 col-md-8">
                  <p>or leave a note, and I'll get back to you</p>
                  <ContactUs formSource={this.state.matchon} />
                </div>
              </div>
            </div>
          </div>
      </React.Fragment>
    );
  }
}

export default Cv;
